import axios from 'axios'
import {apiToCompanyProperty, companyPropertyToApi} from './companyConverters'

const COMPANY_PROPERTIES_API = '/CompanyPropertyValues/'


export default {
  create(company, modelData) {
    modelData.company = company
    var postData = companyPropertyToApi(modelData)

    return axios.post(COMPANY_PROPERTIES_API, postData)
      .then(apiToCompanyProperty)
  },
  update(model) {
    var postData = companyPropertyToApi(model)
    return axios.patch(COMPANY_PROPERTIES_API + postData.id, postData)
  },
  delete(model) {
    return axios.delete(COMPANY_PROPERTIES_API + model.id)
  },
}



