// https://stackoverflow.com/questions/8511281/check-if-a-value-is-an-object-in-javascript/16608074#16608074
// https://www.webbjocke.com/javascript-check-data-types/

function removeNulls(_obj) {
  var obj = Object.assign({}, _obj)
  Object.keys(obj).forEach(key => {
    if (!obj[key]) {
      delete obj[key]
    } else if (obj[key].constructor === Object) {
      var subObj = removeNulls(obj[key])
      if (Object.keys(subObj).length) {
        obj[key] = subObj
      } else {
        delete obj[key]
      }
    }
  })

  if (Object.keys(obj).length) {
    return obj
  } else {
    return null
  }
}

function hasAnyData(obj) {
  return !!Object.keys(obj).find(key => obj[key])
}


function isObject(a) {
  return (!!a) && (a.constructor === Object)
}

function isArray(a) {
  return (!!a) && (a.constructor === Array)
}


export default {
  removeNulls,
  hasAnyData,
  isObject,
  isArray,
}



