import axios from 'axios'
import * as convert from './nomenclatureConverters'
import stringUtils from '@/helpers/stringUtils'
import {COMPANY_PROPERTY_CATEGORIES} from '@/constants'

var nomenclatureAPIs = {}

// Nomenclatures with common format
const NOMENCLATURES_PROPERTIES = [
  // Format: {name, converter, endpoint},
  {name: 'countries', converter: 'apiToCountry'},
  {name: 'currencies', converter: 'apiToCurrency'},
  // These are hardcoded, so they are NOT get from API
  // {name: 'statementTypes'},
  // {name: 'companyPropertyTypes'},
]

NOMENCLATURES_PROPERTIES.reduce((api, nomenclature) => {
  api[nomenclature.name] = function() {
    const endpoint = nomenclature.endpoint || stringUtils.capitalizeFirstLetter(nomenclature.name)
    const promise = axios.get('/' + endpoint)

    return (!nomenclature.converter) ? promise : promise.then(data => data.map(convert[nomenclature.converter]))
  }
  return api
}, nomenclatureAPIs)


function compPropCatKeys(catId) {
  return axios.get('/CompanyPropertyTypes/' + catId + '/keys')
  // There is no converter because we use the same objects
}

Object.keys(COMPANY_PROPERTY_CATEGORIES).reduce((api, catName) => {
  const name = catName + 'Keys'
  api[name] = function() {
    return compPropCatKeys(COMPANY_PROPERTY_CATEGORIES[catName])
  }
  return api
}, nomenclatureAPIs)


export default nomenclatureAPIs



