import merge from 'lodash.merge'

import basePropMixin from './basePropMixin'
import objUtils from '@/helpers/objUtils'

export default function(setName) {
  const setObj = {
    data() {
      return {
        cpModel: Object.assign({}, this.cProp),
        editMode: false,
      }
    },
    computed: {
      hasAnyData() {
        const bool = this.cProp ? objUtils.hasAnyData(this.cProp) : false
        this.editMode = !bool
        return bool
      },
    },
    methods: {
      validate() {
        if (objUtils.hasAnyData(this.cpModel)) {
          return Promise.resolve(true)
        }

        return Promise.reject(new Error('Please, enter some data.'))
      },
      save() {
        var updateData = {}
        updateData[setName] = this.cpModel
        return this.$store.dispatch('updateSelCompany', updateData)
          .then(() => {
            this.$emit('success')
          })
      },
      remove() {
        var updateData = {}
        updateData[setName] = Object.keys(this.cProp).reduce((obj, key) => {
          obj[key] = null
          return obj
        }, {})

        return this.$store.dispatch('updateSelCompany', updateData)
          .then(() => {
            this.$emit('remove')
          })
      },
    },
  }

  return merge({}, basePropMixin, setObj)
}



