import {requireUser} from './guard'

// Pages
import NomenclaturesPage from '@/pages/prototypes/NomenclaturesPage'


export default [
  {
    path: 'nomenclatures',
    name: 'Nomenclatures',
    component: NomenclaturesPage,
    meta: {
      pageId: 'nomenclatures-page',
      pageClass: '',
    },
    beforeEnter: requireUser,
  },
]



