import store from '@/store'


function requireGuest(to, from, next) {
  if (store.getters.token) {
    // The user is logged-in, but must not be
    next({
      path: '/',
    })
  } else {
    next()
  }
}

function requireUser(to, from, next) {
  if (!store.getters.token) {
    // The user is NOT logged-in, but must be
    // Send him to login and then redirect him to to page he wanted to go
    next({
      path: '/login',
      query: {
        redirect: to.fullPath,
      },
    })
  } else if (from.path === '/login' && from.query.redirect && from.query.redirect !== to.path) {
    // The user wanted to go to page where he must to be logged-in but he wasn't.
    // Now he has just login and will be redirected to the page he wanted to go initially.
    next({
      path: from.query.redirect,
    })
  } else {
    next()
  }
}


export {
  requireGuest,
  requireUser,
}



