<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-5 mx-auto pt-5">

        <div class="card" v-if="!isActionSuccessful">
          <h2 class="card-header text-center">{{$t("forgotPageTitle")}}</h2>
          <div class="card-body">
            <forgot-pass-form @success="isActionSuccessful = true"></forgot-pass-form>
          </div>
          <div class="card-footer text-center">
            <router-link to="/login">{{$t("Login")}}</router-link>&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;<router-link to="/register">{{$t("Register")}}</router-link>
          </div>
        </div><!-- .card -->

        <div v-if="isActionSuccessful">
          <p>We have received your request for recovery and have sent a mail to your email address.</p>
          <p>Please, check your email, click on the link and reset your password.</p>
        </div>

      </div>
    </div>
  </div>
</template>


<script>
import ForgotPassForm from '@/components/forms-auth/ForgotPassForm'

export default {
  components: {
    ForgotPassForm,
  },
  data() {
    return {
      isActionSuccessful: false,
    }
  },
}
</script>



