export default {
  methods: {
    runAfterSCisSet(callback) {
      if (this.$store.getters.selectedCompany) {
        callback()
      } else {
        this.$store.watch((state, getters) => {
          return getters.selectedCompany
        }, (newSelectedCompany) => {
          if (newSelectedCompany) {
            callback()
          }
        })
      }
    },
  },
}



