<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-5 mx-auto pt-5">

        <div class="card" v-if="!isActionSuccessful">
          <h2 class="card-header text-center">{{$t("registerPageTitle")}}</h2>
          <div class="card-body">
            <register-form @success="isActionSuccessful = true"></register-form>
          </div>
          <div class="card-footer text-center">
            <router-link to="/login">{{$t("Login")}}</router-link>&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;<router-link to="/forgot">Forgot password?</router-link>
          </div>
        </div><!-- .card -->

        <div v-if="isActionSuccessful">
          <p>We have received your registration and have sent a mail to your email address.</p>
          <p>Please, check your email and confirm the registration by clicking on the link in it.</p>
        </div>

      </div>
    </div>
  </div>
</template>


<script>
import RegisterForm from '@/components/forms-auth/RegisterForm'

export default {
  components: {
    RegisterForm,
  },
  data() {
    return {
      isActionSuccessful: false,
    }
  },
}
</script>



