<template>
  <div class="contact-page">
    <h2>Contacts</h2>

    <button type="button"
          v-if="!showForm"
          class="btn btn-success btn-sm mb-3"
          @click="showForm = true">+ Add</button>

    <contact-form v-if="showForm" @cancel="showForm = false"></contact-form>

    <b-tabs @input="onTabChange">
      <b-tab title="Contacts" active>
        <contacts-table v-if="hasContacts"></contacts-table>
        <div v-if="!hasContacts && !loading">There aren't any contacts added yet.</div>
      </b-tab>
      <b-tab title="Invitations" >
        <invitations-table v-if="hasInvitations"></invitations-table>
        <div v-if="!hasContacts && !loading">There aren't any invitations.</div>
      </b-tab>
    </b-tabs>

    <loading :loading="loading"></loading>

  </div>
</template>


<script>
import runAfterSCisSetMixin from '../runAfterSCisSetMixin'
import ContactsTable from './ContactsTable'
import InvitationsTable from './InvitationsTable'
import ContactForm from './ContactForm'


export default {
  name: 'ContactsPage',
  components: {
    ContactsTable,
    InvitationsTable,
    ContactForm,
  },
  mixins: [runAfterSCisSetMixin],
  created() {
    this.runAfterSCisSet(this.loadContacts)
  },
  data() {
    return {
      showForm: false,
      loading: false,
    }
  },
  computed: {
    hasContacts() {
      return !!this.$store.getters.contacts.length
    },
    hasInvitations() {
      return !!this.$store.getters.companySentInvitations.length
    },
  },
  methods: {
    loadContacts() {
      this.loading = true
      this.$store.dispatch('loadSCContacts')
        .finally(() => {
          this.loading = false
        })
    },
    loadInvitations() {
      this.loading = true
      this.$store.dispatch('loadSCSentInvitations')
        .finally(() => {
          this.loading = false
        })
    },
    onTabChange(index) {
      if (index === 1) {
        this.loadInvitations()
      }
    },
  },
}
</script>



