<template>
  <div class="company-profile">

    <my-company-form :company="company"></my-company-form>

    <h2>Aliases</h2>
    <alias v-for="alias in company.aliases" :key="alias.id" :c-prop="alias"></alias>
    <alias :c-prop="{companyId: company.id, refCompanyId: company.id}"
           v-if="showAliasForm"
           @cancel="showAliasForm = false">
    </alias>
    <button type="button"
            v-if="!showAliasForm"
            class="btn btn-success btn-sm"
            @click="showAliasForm = true">+ Add</button>


    <h2>Corporate Tax</h2>
    <corp-tax :c-prop="company.corpTax"
           v-if="hasAnyCorpTaxData || (!hasAnyCorpTaxData && showCorpTax)"
           @cancel="showCorpTax = false">
    </corp-tax>
    <button type="button"
            v-if="!hasAnyCorpTaxData && !showCorpTax"
            class="btn btn-success btn-sm"
            @click="showCorpTax = true">+ Add</button>


    <h2>Audit</h2>
    <audit :c-prop="company.audit"
           v-if="hasAnyAuditData || (!hasAnyAuditData && showAudit)"
           @cancel="showAudit = false">
    </audit>
    <button type="button"
            v-if="!hasAnyAuditData && !showAudit"
            class="btn btn-success btn-sm"
            @click="showAudit = true">+ Add</button>


    <h2>VAT / Sales Tax</h2>
    <vat v-for="vat in company.vats" :key="vat.id" :c-prop="vat"></vat>
    <vat v-if="showVatForm"
         @cancel="showVatForm = false">
    </vat>
    <button type="button"
            v-if="!showVatForm"
            class="btn btn-success btn-sm"
            @click="showVatForm = true">+ Add</button>


    <h2>Personnel</h2>
    <personnel v-for="responsible in company.personnel" :key="responsible.id" :c-prop="responsible"></personnel>
    <personnel v-if="showPersonnelForm"
         @cancel="showPersonnelForm = false">
    </personnel>
    <button type="button"
            v-if="!showPersonnelForm"
            class="btn btn-success btn-sm"
            @click="showPersonnelForm = true">+ Add</button>


    <h2>Taxes</h2>
    <cp-pair v-for="tax in company.taxes"
         :key="tax.id"
         :c-prop="tax"
         cat="tax">
    </cp-pair>
    <cp-pair v-if="showTaxForm"
         cat="tax"
         @cancel="showTaxForm = false">
    </cp-pair>
    <button type="button"
            v-if="!showTaxForm"
            class="btn btn-success btn-sm"
            @click="showTaxForm = true">+ Add</button>


    <h2>Software</h2>
    <cp-pair v-for="software in company.software"
         :key="software.id"
         :c-prop="software"
         cat="software">
    </cp-pair>
    <cp-pair v-if="showSoftwareForm"
         cat="software"
         @cancel="showSoftwareForm = false">
    </cp-pair>
    <button type="button"
            v-if="!showSoftwareForm"
            class="btn btn-success btn-sm"
            @click="showSoftwareForm = true">+ Add</button>


    <h2>Misc</h2>
    <cp-pair v-for="pair in company.misc"
         :key="pair.id"
         :c-prop="pair"
         cat="misc">
    </cp-pair>
    <cp-pair v-if="showMiscForm"
         cat="misc"
         @cancel="showMiscForm = false">
    </cp-pair>
    <button type="button"
            v-if="!showMiscForm"
            class="btn btn-success btn-sm"
            @click="showMiscForm = true">+ Add</button>


  </div>
</template>


<script>
import objUtils from '@/helpers/objUtils'

import MyCompanyForm from '@/components/forms-company/MyCompanyForm'

import Alias from './cp-forms/Alias'
import CorpTax from './cp-forms/CorpTax'
import Audit from './cp-forms/Audit'
import Vat from './cp-forms/Vat'
import Personnel from './cp-forms/Personnel'
import CpPair from './cp-forms/CpPair'


export default {
  components: {
    MyCompanyForm,
    Alias,
    CorpTax,
    Audit,
    Vat,
    Personnel,
    CpPair,
  },
  data() {
    return {
      showAliasForm: false,
      showCorpTax: false,
      showAudit: false,
      showVatForm: false,
      showPersonnelForm: false,
      showTaxForm: false,
      showSoftwareForm: false,
      showMiscForm: false,
      loading: false,
    }
  },
  computed: {
    company() {
      return this.$store.getters.selectedCompany || {}
    },
    hasAnyCorpTaxData() {
      return this.company.corpTax ? objUtils.hasAnyData(this.company.corpTax) : false
    },
    hasAnyAuditData() {
      return this.company.audit ? objUtils.hasAnyData(this.company.audit) : false
    },
  },
}
</script>



