<template>
  <form @submit.prevent="submit" class="upload-form">

    <div class="alert alert-danger text-center" v-if="errMsg">
      {{ errMsg }}
    </div>

    <div class="form-group">
      <label for="statementType">Statement type:</label>
      <c-form-select id="statementType"
                    name="statementType"
                    v-validate="'required'"
                    :class="[{ 'is-invalid': errors.has('statementType')}]"
                    v-model="formData.statementType"
                    :plain="true"
                    :options="statementTypes">
      </c-form-select>
    </div>


    <div class="text-center py-3">
      <button type="submit" class="btn btn-primary">Upload</button>
      <button type="button" class="btn btn-light" @click="cancel">Cancel</button>
    </div>

    <loading :loading="loading"></loading>

  </form>
</template>


<script>
export default {
  data() {
    return {
      formData: {},
      errMsg: null,
      loading: false,
    }
  },
  computed: {
    statementTypes() {
      return this.$store.getters.statementTypes
    },
  },
  methods: {
    submit() {
      this.loading = true
      this.$validator.validateAll()
        .then(result => {
          if (result) {
            return this.$store.dispatch('TODO', this.formData)
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .then(res => {
          this.$emit('success', res)
        })
        .catch(errRes => {
          this.errMsg = this.$t(errRes.message)
        })
        .finally(() => {
          this.loading = false
        })
    },
    cancel() {
      this.$emit('cancel')
      this.errMsg = null
    },
  },
}
</script>



