import axios from 'axios'
import {apiToVat, vatToApi} from './companyConverters'

const VATS_API = '/Vats/'


export default {
  create(company, modelData) {
    modelData.company = company
    var postData = vatToApi(modelData)

    return axios.post(VATS_API, postData)
      .then(apiToVat)
  },
  update(model) {
    var postData = vatToApi(model)
    return axios.patch(VATS_API + postData.id, postData)
  },
  delete(model) {
    return axios.delete(VATS_API + model.id)
  },
}



