<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-6 mx-auto pt-5">

        <div class="card">
          <h2 class="card-header text-center">{{$t("Add Company")}}</h2>
          <div class="card-body">
            <my-company-form @success="onAddedCompany" @cancel="cancel"></my-company-form>
          </div>
        </div><!-- .card -->

      </div>
    </div>
  </div>
</template>


<script>
import MyCompanyForm from '@/components/forms-company/MyCompanyForm'

export default {
  name: 'AddCompany',
  components: {
    MyCompanyForm,
  },
  methods: {
    onAddedCompany(company) {
      this.$router.push({name: 'CompanyProfile', params: {id: company.id}})
    },
    cancel() {
      this.$router.go(-1)
    },
  },
}
</script>



