<template>
  <form @submit.prevent="submit" class="reset-pass-form">

    <div class="alert alert-danger text-center" v-if="errMsg">
      {{ errMsg }}
    </div>

    <div class="form-group inline-form-group">
      <label class="col-form-label">{{$t("Password")}}:</label>
      <div>
        <input type="password"
          class="form-control"
          :class="[{ 'is-invalid': !$v.formData.password.required && $v.formData.password.$dirty }]"
          @blur="$v.formData.password.$touch()"
          v-model="formData.password">
        <div class="invalid-feedback">{{$t("Please, enter the password")}}</div>
      </div>
    </div>

    <div class="form-group inline-form-group">
      <label class="col-form-label">{{$t("Repeat Password")}}:</label>
      <div>
        <input type="password"
          class="form-control"
          :class="[{ 'is-invalid': !$v.formData.repPassword.sameAsPass && $v.formData.repPassword.$dirty }]"
          @blur="$v.formData.repPassword.$touch()"
          v-model="formData.repPassword">
        <div class="invalid-feedback">
          {{ $v.formData.repPassword.sameAsPass ? $t("Repeated password is not the same") : $t("Please, repeat the password") }}
        </div>
      </div>
    </div>

    <div class="text-center py-3">
      <button type="submit" class="btn btn-success" :disabled="$v.formData.$invalid">{{$t("resetFormSubmit")}}</button>
    </div>

    <loading :loading="loading"></loading>

  </form>
</template>


<script>
import { required, sameAs } from 'vuelidate/lib/validators'
import userService from '@/services/userService'

// TODO: pass min 8 symbols

export default {
  props: ['token'],
  validations: {
    formData: {
      password: {
        required,
      },
      repPassword: {
        required,
        sameAsPass: sameAs('password'),
      },
    },
  },
  data() {
    return {
      formData: {
        password: null,
        repPassword: null,
      },
      loading: false,
      errMsg: null,
    }
  },
  methods: {
    submit() {
      if (!this.$v.formData.$invalid) {
        this.loading = true
        userService.resetPass(this.token, this.formData.password)
          .then(res => {
            this.$emit('success')
          })
          .catch(errRes => {
            console.log(errRes)
            this.errMsg = this.$t(errRes.message)
          })
          .finally(() => {
            this.loading = false
          })
      }
    },
  },
}
</script>



