import sorter from '@/helpers/sorter'

const BASE_CLASS_NAME = 'sort'
const ASC_CLASS_NAME = 'sort-asc'
const DESC_CLASS_NAME = 'sort-desc'

export default function(store) {
  return {
    bind(el, binding, vnode) {
      var config = binding.value

      var propPath = config.sortKey
      var compare = config.compare
      var initOrder = config.initOrder
      var order = null

      el.classList.add(BASE_CLASS_NAME)

      function clickHandler() {
        if (order === null || order === -1) {
          // ASC
          order = 1
          el.classList.add(ASC_CLASS_NAME)
          el.classList.remove(DESC_CLASS_NAME)
        } else {
          // DESC
          order = -1
          el.classList.add(DESC_CLASS_NAME)
          el.classList.remove(ASC_CLASS_NAME)
        }

        const payload = {
          sortId: propPath + order,
          comparator: sorter(compare, propPath, order),
          onKeyChangeCallback: reset,
        }
        store.commit('setSorter', payload)
      }

      function reset(newSortId) {
        if (newSortId.startsWith(propPath)) {
          // The same sort, just the order is different
          return
        }
        // New sort key
        el.classList.remove(DESC_CLASS_NAME)
        el.classList.remove(ASC_CLASS_NAME)
        order = null
      }

      el.addEventListener('click', clickHandler)

      if (initOrder) {
        // Set initial order
        order = (initOrder === 'DESC') ? 1 : -1
        clickHandler()
      }
    },
  }
}



