<template>
  <div>
    <h2>Collaborators</h2>

    <button type="button"
          v-if="!showForm"
          class="btn btn-success btn-sm"
          @click="showForm = true">+ Add</button>

    <collaborator-form v-if="showForm" @cancel="showForm = false"></collaborator-form>

    <collaborators-table class="mt-3"></collaborators-table>

    <loading :loading="loading"></loading>

  </div>
</template>


<script>
// import tableTools from '@/table-tools'
// import {options} from './CollaboratorsTable'
import runAfterSCisSetMixin from '../runAfterSCisSetMixin'
import CollaboratorForm from './CollaboratorForm'
import CollaboratorsTable from './CollaboratorsTable'


export default {
  components: {
    CollaboratorForm,
    CollaboratorsTable,
  },
  mixins: [runAfterSCisSetMixin],
  created() {
    this.runAfterSCisSet(this.loadCollaborators)
  },
  data() {
    return {
      showForm: false,
      loading: false,
    }
  },
  // computed: {
  //   collaborators() {
  //     return this.$store.getters.collaborators
  //   },
  // },
  methods: {
    loadCollaborators() {
      this.loading = true
      this.$store.dispatch('loadSCCollaborators')
        // .then((newCollaborators) => {
        //   if (newCollaborators) {
        //     tableTools.tableStore.commit('setItems', newCollaborators)
        //   }
        // })
        .finally(() => {
          this.loading = false
        })
    },
  },
}
</script>



