import idMixin from 'bootstrap-vue/es/mixins/id'
import formOptionsMixin from '../mixins/form-options'
import formMixin from 'bootstrap-vue/es/mixins/form'
import formSizeMixin from 'bootstrap-vue/es/mixins/form-size'
import formStateMixin from 'bootstrap-vue/es/mixins/form-state'
import formCustomMixin from 'bootstrap-vue/es/mixins/form-custom'
import { from as arrayFrom } from 'bootstrap-vue/es/utils/array'


export default {
  mixins: [idMixin, formMixin, formSizeMixin, formStateMixin, formCustomMixin, formOptionsMixin],
  render: function render(h) {
    var t = this
    var $slots = t.$slots
    var options = t.formOptions.map(function(option, index) {
      return h('option', {
        key: 'option_' + index + '_opt',
        attrs: { disabled: Boolean(option.disabled) },
        domProps: { innerHTML: option.text, value: option.value },
      })
    })

    return h('select', {
      ref: 'input',
      class: t.inputClass,
      directives: [{
        name: 'model',
        rawName: 'v-model',
        value: t.localValue ? t.localValue[this.valueField] : t.localValue, // works for !isMultiple
        expression: 'localValue',
      }],
      attrs: {
        id: t.safeId(),
        name: t.name,
        multiple: t.multiple || null,
        size: t.isMultiple ? t.selectSize : null,
        disabled: t.disabled,
        required: t.required,
        'aria-required': t.required ? 'true' : null,
        'aria-invalid': t.computedAriaInvalid,
      },
      on: {
        change: function change(evt) {
          var target = evt.target
          var selectedVal = arrayFrom(target.options).reduce(function(arr, option, index) {
            if (option.selected) {
              arr.push(index)
            }
            return arr
          }, [])
          t.localValue = target.multiple ? selectedVal : t.allOptions[selectedVal[0]] // works for !isMultiple
          t.$emit('change', t.localValue)
        },
      },
    }, [$slots.first, options, $slots.default])
  },
  data: function data() {
    return {
      localValue: this.value,
    }
  },

  watch: {
    value: function value(newVal, oldVal) {
      this.localValue = newVal
    },
    localValue: function localValue(newVal, oldVal) {
      this.$emit('input', this.localValue)
    },
  },
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: false,
    },
    selectSize: {
      // Browsers default size to 0, which shows 4 rows in most browsers in multiple mode
      // Size of 1 can bork out firefox
      type: Number,
      default: 0,
    },
    ariaInvalid: {
      type: [Boolean, String],
      default: false,
    },
    slotOptions: {
      type: [Array],
      default() {
        return []
      },
    },
  },
  computed: {
    inputClass: function inputClass() {
      return ['form-control', this.stateClass, this.sizeFormClass,
        // Awaiting for https://github.com/twbs/bootstrap/issues/23058
        this.isPlain ? null : 'custom-select', this.isPlain || !this.size ? null : 'custom-select-' + this.size]
    },
    isPlain: function isPlain() {
      return this.plain || this.isMultiple
    },
    isMultiple: function isMultiple() {
      return Boolean(this.multiple && this.selectSize > 1)
    },
    computedAriaInvalid: function computedAriaInvalid() {
      if (this.ariaInvalid === true || this.ariaInvalid === 'true') {
        return 'true'
      }
      return this.stateClass === 'is-invalid' ? 'true' : null
    },
    allOptions() {
      return this.options.concat(this.slotOptions)
    },
  },
}



