<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!cProp.name"
           @edit="onEdit">

    <div class="cp-row">
      <label>Responsibility:</label>
      <b-form-select class="form-control"
                      name="responsibility"
                      v-validate="'required'"
                      :class="[{ 'is-invalid': errors.has('responsibility')}]"
                      v-model="cpModel.responsibility.id"
                      :disabled="!editMode"
                      :plain="true">
        <option v-for="responsibility in responsibilityKeys" :value="responsibility.id">{{ responsibility.label }}</option>
      </b-form-select>
    </div>


    <div class="cp-row">
      <label>Name:</label>
      <input
             type="text"
             class="form-control"
             v-model="cpModel.name"
             name="name"
             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('name')}]"
             :disabled="!editMode" />
    </div>


    <div class="cp-row">
      <label>Email:</label>
      <input
             type="text"
             class="form-control"
             v-model="cpModel.email"
             name="email"
             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('email')}]"
             :disabled="!editMode" />
    </div>


    <div class="cp-row">
      <label>Phone:</label>
      <input
             type="text"
             class="form-control"
             v-model="cpModel.phone"
             name="phone"
             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('phone')}]"
             :disabled="!editMode" />
    </div>

  </cp-form>
</template>


<script>
import merge from 'lodash.merge'
import basePropMixin from './basePropMixin'

const vatVueConfig = {
  created() {
    // TODO: ?? does it work, shoud use $ref na CpForm??
    this.loading = true
    this.$store.dispatch('loadResponsibilityKeys')
      .finally(() => {
        this.loading = false
      })
  },
  props: {
    cProp: {
      required: false,
      default() {
        return {responsibility: {}}
      },
    },
  },
  data() {
    return {
      cpModel: Object.assign({}, this.cProp),
      editMode: !this.cProp.name,
    }
  },
  computed: {
    responsibilityKeys() {
      return this.$store.getters.responsibilityKeys
    },
  },
  methods: {
    save() {
      const action = this.cProp.num ? 'updateSCPersonnel' : 'createSCPersonnel'
      return this.$store.dispatch(action, this.cpModel)
        .then(() => {
          this.$emit('success')
        })
    },
    remove() {
      return this.$store.dispatch('deleteSCPersonnel', this.cProp)
        .then(() => {
          this.$emit('remove')
        })
    },
  },
}

export default merge({}, basePropMixin, vatVueConfig)
</script>



