import axios from 'axios'
import {apiToPersonnel, personnelToApi} from './companyConverters'

const PERSONNEL_API = '/Personnel/'


export default {
  create(company, modelData) {
    modelData.company = company
    var postData = personnelToApi(modelData)

    return axios.post(PERSONNEL_API, postData)
      .then(apiToPersonnel)
  },
  update(model) {
    var postData = personnelToApi(model)
    return axios.patch(PERSONNEL_API + postData.id, postData)
  },
  delete(model) {
    return axios.delete(PERSONNEL_API + model.id)
  },
}



