<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!hasAnyData"
           @edit="onEdit">

    <div class="cp-row">
      <label for="auditorName">Name:</label>
      <input id="auditorName"
             type="text"
             class="form-control"
             v-model="cpModel.name"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="auditorEmail">Email:</label>
      <input id="auditorEmail"
             type="text"
             class="form-control"
             v-model="cpModel.email"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="auditorPhone">Phone:</label>
      <input id="auditorPhone"
             type="text"
             class="form-control"
             v-model="cpModel.phone"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="auditorDdl">Deadline:</label>
      <input id="auditorDdl"
             type="text"
             class="form-control"
             v-model="cpModel.ddl"
             :disabled="!editMode" />
    </div>

  </cp-form>
</template>


<script>
import singlePropSetMixin from './singlePropSetMixin'

export default {
  mixins: [singlePropSetMixin('audit')],
}
</script>



