export default {
  methods: {
    runAfterNomenclatureIsSet(nomenclature, callback) {
      if (this.$store.getters[nomenclature]) {
        callback()
      } else {
        var stopWatching = this.$store.watch((state, getters) => {
          return getters[nomenclature]
        }, (val) => {
          if (val) {
            callback()
            stopWatching()
          }
        })
      }
    },
  },
}



