// * * * API TO MODEL * * *
function apiToCollaborator(data) {
  return {
    name: data.fullName || '',
    role: {
      id: data.roleId,
      label: data.roleLabel,
    },
    company: {
      id: data.companyId,
    },
    email: data.userEmail,
    isPending: data.isPending,
  }
}


// * * * MODEL TO API * * *
function collaboratorToApi(model) {
  var data = {
    roleId: model.role.id,
    userEmail: model.email,
  }

  if (model.name) {
    data.fullName = model.name
  }

  if (model.company) {
    data.companyId = model.company.id
  }

  return data
}


export {
  // API TO MODEL
  apiToCollaborator,
  // MODEL TO API
  collaboratorToApi,
}



