import {memoizedLookupFunction} from './functions'

const comparators = {
  strings(a, b, order) {
    var stringA = a.toUpperCase().trim()
    var stringB = b.toUpperCase().trim()
    if (stringA < stringB) {
      return order * -1
    } else if (stringA > stringB) {
      return order * 1
    } else {
      return 0
    }
  },
  numbers(a, b, order) {
    return order * (a - b)
  },
  dates(a, b, order) {
    var ta = a ? a.getTime() : 0
    var tb = b ? b.getTime() : 0
    return order * (ta - tb)
  },
  bool(a, b, order) {
    var w = a ? 1 : 0
    var x = b ? 1 : 0
    return order * (w - x)
  },
}

export default function(type, propPath, ord) {
  var derefFuncs = {}
  return function(objA, objB) {
    return comparators[type](memoizedLookupFunction(objA, propPath, derefFuncs), memoizedLookupFunction(objB, propPath, derefFuncs), ord)
  }
}



