<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!cProp.aname"
           @edit="onEdit">

    <input type="text"
           class="form-control"
           name="aname"
           v-validate="'required'"
           :class="[{ 'is-invalid': errors.has('aname')}]"
           v-model="cpModel.aname"
           :disabled="!editMode" />

  </cp-form>
</template>


<script>
import merge from 'lodash.merge'
import basePropMixin from './basePropMixin'

const aliasVueConfig = {
  data() {
    return {
      cpModel: Object.assign({}, this.cProp),
      editMode: !this.cProp.aname,
    }
  },
  methods: {
    save() {
      var promise
      if (this.cProp.aname) {
        // UPDATE
        promise = this.$store.dispatch('updateSCAlias', {
          alias: this.cProp,
          updatedAname: this.cpModel.aname,
        })
      } else {
        // CREATE
        promise = this.$store.dispatch('createSCAlias', this.cpModel.aname)
      }
      return promise
        .then(() => {
          this.$emit('success')
        })
    },
    remove() {
      return this.$store.dispatch('deleteSCAlias', this.cProp)
        .then(() => {
          this.$emit('remove')
        })
    },
  },
}

export default merge({}, basePropMixin, aliasVueConfig)
</script>



