<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!hasAnyData"
           @edit="onEdit">

    <div class="cp-row">
      <label for="corpTaxId">Corporate Tax ID:</label>
      <input id="corpTaxId"
             type="text"
             class="form-control"
             v-model="cpModel.num"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="corpTaxFillingDate">Filing Deadline:</label>
      <input id="corpTaxFillingDate"
             type="text"
             class="form-control"
             v-model="cpModel.fillingDate"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="corpTaxDueDate">Due Date:</label>
      <input id="corpTaxDueDate"
             type="text"
             class="form-control"
             v-model="cpModel.dueDate"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="corpTaxAdvancedPayments">Advanced Payments: to do ...:</label>
      <input id="corpTaxAdvancedPayments"
             type="text"
             class="form-control"
             v-model="cpModel.advancedPayments"
             :disabled="!editMode" />
    </div>

  </cp-form>
</template>


<script>
import singlePropSetMixin from './singlePropSetMixin'

export default {
  mixins: [singlePropSetMixin('corpTax')],
}
</script>



