import userService from '@/services/userService'
import router from '@/router'

const USER_DATA_STORATGE_KEY = 'ud'
const AUTH_TOKEN_STORATGE_KEY = 'at'


const state = {
  user: null,
  token: null,
}

const getters = {
  user(state) {
    return state.user
  },
  token(state) {
    return state.token
  },
}

const mutations = {
  setToken(state, payload) {
    state.token = payload
  },
  setUser(state, payload) {
    state.user = payload
  },
  clearout(state) {
    state.user = null
    state.token = null
  },
}

const actions = {
  loadUserCash({commit}) {
    const token = sessionStorage.getItem(AUTH_TOKEN_STORATGE_KEY)
    if (token) {
      commit('setToken', token)
    }
    const userData = JSON.parse(sessionStorage.getItem(USER_DATA_STORATGE_KEY))
    if (userData) {
      commit('setUser', userData)
    }
  },
  register({commit}, formData) {
    return userService.register(formData)
  },
  login({commit}, credentials) {
    return userService.login(credentials)
      .then(userData => {
        sessionStorage.setItem(USER_DATA_STORATGE_KEY, JSON.stringify(userData))
        commit('setUser', userData)
      })
  },
  logout({dispatch}) {
    return userService.logout()
      .finally(() => {
        dispatch('clearout')
      })
  },
  clearout({commit}) {
    commit('clearout')
    router.push('/login')
    sessionStorage.clear()
  },
  setToken({commit}, token) {
    commit('setToken', token)
    sessionStorage.setItem(AUTH_TOKEN_STORATGE_KEY, token)
  },
}

export default {
  state,
  mutations,
  actions,
  getters,
}



