<template>
  <cp-form :save="save"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="editMode"
           @edit="onEdit"
           class="contact-form"
           ref="form">

    <div class="form-row">
      <div class="form-group col">
        <label for="cf-name">Company Name:</label>
        <input type="text"
              id="cf-name"
              class="form-control"
              name="name"
              v-validate="'required'"
              :class="[{ 'is-invalid': errors.has('name')}]"
              v-model="cpModel.name" />
        <div class="invalid-feedback">{{ $t("Please, enter a valid name") }}.</div>
        <small class="form-text text-muted">The complete registered name.</small>
      </div>

      <div class="form-group col">
        <label for="cf-country">Country:</label>
        <c-form-select class="form-control"
                      id="cf-country"
                      name="country"
                      v-validate="'required'"
                      :class="[{ 'is-invalid': errors.has('country')}]"
                      v-model="cpModel.country"
                      :plain="true"
                      :options="countries"
                      text-field="name">
        </c-form-select>
        <div class="invalid-feedback">{{ $t("Please, select a country") }}.</div>
      </div>

      <div class="form-group col">
        <label for="cf-regNum">Registration number:</label>
        <input type="text"
              id="cf-regNum"
              class="form-control"
              name="regNum"
              v-validate="'required'"
              :class="[{ 'is-invalid': errors.has('regNum')}]"
              v-model="cpModel.regNum" />
        <div class="invalid-feedback">{{ $t("Please, enter company's registration number") }}.</div>
      </div>

      <div class="form-group col">
        <label for="cf-email">Contact Person Email:</label>
        <input type="text"
              id="cf-email"
              class="form-control"
              name="email"
              v-validate="'required|email'"
              :class="[{ 'is-invalid': errors.has('email')}]"
              v-model="cpModel.email" />
        <div class="invalid-feedback">{{ $t("Please, enter a valid email") }}.</div>
      </div>

    </div><!-- form-row -->

  </cp-form>
</template>


<script>
import merge from 'lodash.merge'
import runAfterNomenclatureIsSetMixin from '../runAfterNomenclatureIsSetMixin'
import basePropMixin from '../profile/cp-forms/basePropMixin'

const vueComponentConfig = {
  mixins: [runAfterNomenclatureIsSetMixin],
  created() {
    this.$store.dispatch('loadCountries')
  },
  mounted() {
    this.$refs.form.loading = true
    this.runAfterNomenclatureIsSet('countries', () => {
      this.$refs.form.loading = false
    })
  },
  props: {
    cProp: {
      required: false,
    },
  },
  data() {
    return {
      cpModel: Object.assign({}, this.cProp),
      editMode: true,
    }
  },
  computed: {
    countries() {
      return this.$store.getters.countries
    },
  },
  methods: {
    save() {
      return this.$store.dispatch('inviteContact', this.cpModel)
        .then(() => {
          this.$emit('cancel')
        })
    },
  },
}

export default merge({}, basePropMixin, vueComponentConfig)
</script>



