<template>
  <body :class="{ debug: isDev }">

    <header>
      <b-navbar toggleable="md" type="light" variant="light" id="top-bar">
        <b-navbar-brand to="/">Balance Share</b-navbar-brand>

        <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

        <b-collapse is-nav id="nav_collapse">

          <!-- GUEST -->
          <b-navbar-nav v-if="!user">
            <b-nav-item to="/login">Login</b-nav-item>
            <b-nav-item to="/register">Register</b-nav-item>
          </b-navbar-nav>

          <!-- LOGGED USER -->
          <b-navbar-nav class="ml-auto">
            <b-nav-item-dropdown text="DEV PAGES" right>
              <b-dropdown-item to="/prototype/nomenclatures" exact>Nomenclatures</b-dropdown-item>
              <b-dropdown-item to="/matching-tool" exact>Matching Tool</b-dropdown-item>
              <b-dropdown-item to="/forgot">Forgot pass</b-dropdown-item>
              <b-dropdown-item to="/reset">Reset pass</b-dropdown-item>
            </b-nav-item-dropdown>

            <b-nav-item-dropdown :text="i18n.lang" right>
              <b-dropdown-item @click="$i18n.locale = 'en'">English</b-dropdown-item>
              <b-dropdown-item @click="$i18n.locale = 'bg'"></b-dropdown-item>
            </b-nav-item-dropdown>
            <template v-if="user">
              <b-nav-item-dropdown :text="user.name" right>
                <b-dropdown-item to="/change">{{$t("Change Password")}}</b-dropdown-item>
              </b-nav-item-dropdown>
              <b-nav-item @click="logout">{{$t("Logout")}}</b-nav-item>
            </template>
          </b-navbar-nav>

        </b-collapse>

      </b-navbar>
    </header>

    <router-view></router-view>

    <footer>
    </footer>


    <div v-if="isDev" id="dev-toolbox">
      <b-btn v-b-toggle.collapseDevToolbox variant="outline-danger">* Dev Toolbox</b-btn>
      <b-collapse id="collapseDevToolbox" class="mt-2">
        <b-card>
          <button type="button" class="btn btn-danger btn-sm" @click="test">Test Action</button>
          <div slot="footer">
            {{ buildTimestamp }}
          </div>
        </b-card>
      </b-collapse>
    </div>

  </body>
</template>


<script>
export default {
  data() {
    return {
      i18n: {
        lang: this.$t('lang'),
      },
      isDev: process.env.APP_DEBUG,
      buildTimestamp: process.env.BUILD_TIMESTAMP,
    }
  },
  methods: {
    test() {
      console.log('TEST')
    },
    logout() {
      this.$store.dispatch('logout')
    },
  },
  computed: {
    user() {
      return this.$store.getters.user
    },
  },
  watch: {
    '$i18n.locale': function() {
      this.i18n.lang = this.$t('lang')
    },
  },
}
</script>



