import Vue from 'vue'

// !! Import always before all components.
import '@/styles/main-begin.scss'

// NODE MODULES
import VeeValidate from 'vee-validate'
import Vuelidate from 'vuelidate'
import BootstrapVue from 'bootstrap-vue'

// APP CONFIGS
import store from '@/store'
import '@/configAxios'
import router from '@/router'
import i18n from '@/configi18n'

// APP COMPONENTS
import App from '@/App'
import Loading from '@/components/Loading'
import cFormSelect from '@/custom-bootstrap-vue/components/form-select'

// !! Import always after all components.
import '@/styles/main-end.scss'


// VUE CONFIG
Vue.config.productionTip = !process.env.APP_DEBUG

// USE PLUGINS
Vue.use(VeeValidate)
Vue.use(Vuelidate)
Vue.use(BootstrapVue)

// GLOBAL COMPONENTS
Vue.component('loading', Loading)
Vue.component('c-form-select', cFormSelect)


store.dispatch('loadUserCash')


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n,
  render: h => h(App),
})



