<template>
  <tr>
    <template v-if="!loading">
      <td>{{ model.name }}</td>
      <td>{{ model.email }}</td>
      <td>
          <c-form-select class="form-control"
                        v-model="model.role"
                        :plain="true"
                        :options="roles"
                        :disabled="!editMode">
          </c-form-select>
      </td>

      <td>{{ model.isPending ? 'pending' : '' }}</td>

      <td class="actions-col">
        <div v-if="!editMode">
          <button type="button" class="btn btn-info btn-sm" @click="edit">Edit</button>
          <button type="button" class="btn btn-danger btn-sm" @click="remove">Remove</button>
        </div>
        <div v-if="editMode">
          <button type="button" class="btn btn-primary btn-sm" @click="save">Save</button>
          <button type="button" class="btn btn-default btn-sm" @click="cancel">Cancel</button>
        </div>
      </td>

    </template>

    <td v-if="loading" colspan="4">
      <loading :loading="loading"></loading>
    </td>

  </tr>
</template>


<script>
// import tableTools from '@/table-tools'

export default {
  name: 'CollaboratorsTableRow',
  props: {
    collaborator: {
      required: true,
    },
  },
  data() {
    return {
      model: Object.assign({}, this.collaborator),
      editMode: false,
      loading: false,
    }
  },
  computed: {
    roles() {
      return this.$store.getters.roles
    },
  },
  methods: {
    save() {
      return this.$store.dispatch('updateCollaborator', this.model)
        .then(() => {

        })
    },
    edit() {
      this.editMode = true
    },
    cancel() {
      this.editMode = false
    },
    remove() {
      // TODO:
    },
  },
}
</script>



