import axios from 'axios'
import {apiToCollaborator, collaboratorToApi} from './collaboratorConverters'

const COLLABORATORS_API = '/Collaborators/'


export default {
  getCompanyCollaborators(company) {
    return axios.get(COLLABORATORS_API + 'Companies/' + company.id)
      .then(data => data.map(apiToCollaborator))
  },
  create(company, modelData) {
    modelData.company = company
    var postData = collaboratorToApi(modelData)

    return axios.post(COLLABORATORS_API, postData)
      .then(apiToCollaborator)
  },
  update(model) {
    var postData = collaboratorToApi(model)
    return axios.put(COLLABORATORS_API, postData)
  },
  remove(model) {
    return axios.delete(COLLABORATORS_API + 'Companies/' + model.company.id + '/Users/' + model.email)
  },
}



