import axios from 'axios'
import {apiToAlias} from './companyConverters'

// TODO: use aliasToApi , import {apiToAlias, aliasToApi} from './companyConverters'

const ALIASES_API = '/Aliases/'


export default {
  create(company, refCompany, aname) {
    const postData = {
      companyId: company.id,
      refCompanyId: refCompany.id,
      aliasText: aname,
    }

    return axios.post(ALIASES_API, postData)
      .then(apiToAlias)
  },
  update(alias, updatedAname) {
    const updatedData = {
      aliasText: updatedAname,
    }
    return axios.patch(ALIASES_API + alias.id, updatedData)
  },
  delete(model) {
    return axios.delete(ALIASES_API + model.id)
  },
}



