// NOMENCLATURES

function convert(data) {
  return {
    id: data.id,
    name: data.name,
    code: data.isoCode,
    order: data.sortOrder,
  }
}

function apiToCountry(data) {
  return convert(data)
}

function apiToCurrency(data) {
  return convert(data)
}

export {
  apiToCountry,
  apiToCurrency,
}



