<template>
  <div class="container-fluid">

      <div v-for="nomenclature in nomenclatures" :key="nomenclature.name" class="m-4">
        <b-btn v-b-toggle="nomenclature.name" class="mb-2">{{ nomenclature.name }} ({{ count(nomenclature.items) }})</b-btn>
        <b-collapse :id="nomenclature.name">
          <b-card>
            <pre>{{ nomenclature.items }}</pre>
          </b-card>
        </b-collapse>
      </div>

      <loading :loading="loading"></loading>

  </div>
</template>


<script>
import nomenclatureStore from '@/store/nomenclatureStore'
import stringUtils from '@/helpers/stringUtils'
import objUtils from '@/helpers/objUtils'


export default {
  created() {
    this.loading = true

    var promises = Object.keys(nomenclatureStore.state).map(name => {
      var storeItems = nomenclatureStore.state[name]
      if (storeItems) {
        this.nomenclatures.push({name, items: storeItems})
      } else {
        this.$store.dispatch('load' + stringUtils.capitalizeFirstLetter(name))
          .then(loadedItems => { this.nomenclatures.push({name, items: loadedItems}) })
      }
    })

    Promise.all(promises)
      .finally(() => {
        this.loading = false
      })
  },
  data() {
    return {
      nomenclatures: [],
      loading: false,
    }
  },
  methods: {
    count(items) {
      if (objUtils.isObject(items)) {
        return Object.keys(items).length
      }
      return items.length
    },
  },
}
</script>



