<template>
  <cp-form :save="save"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="editMode"
           @edit="onEdit"
           class="collaborator-form">

    <div class="form-row">
      <div class="form-group col">
        <label>Collaborator's Email:</label>
        <input type="text"
              class="form-control"
              name="email"
              v-validate="'required|email'"
              :class="[{ 'is-invalid': errors.has('email')}]"
              v-model="cpModel.email" />
        <div class="invalid-feedback">{{ $t("Please, enter a valid email") }}.</div>
      </div>

      <div class="form-group col">
        <label for="role">Role:</label>
        <c-form-select class="form-control"
                      name="role"
                      v-validate="'required'"
                      :class="[{ 'is-invalid': errors.has('role')}]"
                      v-model="cpModel.role"
                      :plain="true"
                      :options="roles">
        </c-form-select>
        <div class="invalid-feedback">{{ $t("Please, select a role") }}.</div>
      </div>
    </div><!-- form-row -->

  </cp-form>
</template>


<script>
import merge from 'lodash.merge'
import basePropMixin from '../profile/cp-forms/basePropMixin'

const vueComponentConfig = {
  props: {
    cProp: {
      required: false,
    },
  },
  data() {
    return {
      cpModel: Object.assign({}, this.cProp),
      editMode: true,
    }
  },
  computed: {
    roles() {
      return this.$store.getters.roles
    },
  },
  methods: {
    save() {
      return this.$store.dispatch('addSCCollaborator', this.cpModel)
        .then(() => {
          this.$emit('cancel')
        })
    },
  },
}

export default merge({}, basePropMixin, vueComponentConfig)
</script>



