<template>
  <div class="container-fluid">

    <!-- <div class="row">

      <div class="col">

      </div>

    </div> -->

    <div class="row">
      <div class="col-2">

        <nav class="nav flex-column nav-pills">
          <router-link :to="'/companies/' + id + '/upload'" class="nav-link">Upload</router-link>
          <router-link :to="'/companies/' + id + '/contacts'" class="nav-link">Contacts</router-link>
          <router-link :to="'/companies/' + id + '/profile'" class="nav-link">Profile</router-link>
          <router-link :to="'/companies/' + id + '/collaborators'" class="nav-link">Collaborators</router-link>
        </nav>

      </div>

      <div class="col-10">
        <h1>{{ company.name }}</h1>
        <router-view></router-view>
      </div>

    </div>

    <loading :loading="loading"></loading>

  </div>
</template>


<script>
export default {
  props: ['id'],
  created() {
    this.loading = true
    this.$store.dispatch('loadSelCompany', this.id)
      .finally(() => {
        this.loading = false
      })
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    company() {
      return this.$store.getters.selectedCompany || {}
    },
  },
}
</script>



