<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col mx-auto pt-5">

        <h1>Companies</h1>
        <div v-if="myCompanies.length">
          <router-link to="/add-company" class="btn btn-success btn-sm">{{$t("Add Company")}}</router-link>
          <companies-table :companies="myCompanies"></companies-table>

          <loading :loading="loading"></loading>

        </div>
        <div v-if="!myCompanies.length" class="text-center">
          <p>Add your first company : )</p>
          <router-link to="/add-company" class="btn btn-success btn-lg">{{$t("Add Company")}}</router-link>
        </div>

      </div>
    </div>
  </div>
</template>


<script>
import CompaniesTable from '@/components/tables/CompaniesTable'

export default {
  components: {
    CompaniesTable,
  },
  created() {
    this.loading = true
    this.$store.dispatch('loadMyCompanies')
      .finally(() => {
        this.loading = false
      })
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    myCompanies() {
      return this.$store.getters.myCompanies
    },
  },
}
</script>



