<template>
  <form @submit.prevent="submit" class="forgot-pass-form">

    <div class="alert alert-danger text-center" v-if="errMsg">
      {{ errMsg }}
    </div>

    <div class="form-group inline-form-group">
      <label class="col-form-label">{{$t("Email")}}:</label>
      <div>
        <input type="text"
          class="form-control"
          :class="[{ 'is-invalid': (!$v.formData.email.required || !$v.formData.email.email) && $v.formData.email.$dirty }]"
          @blur="$v.formData.email.$touch()"
          v-model="formData.email">
        <div class="invalid-feedback">{{ $v.formData.email.required ? 'Please, enter valid email' : 'Please, enter email' }}</div>
      </div>
    </div>

    <div class="text-center py-3">
      <button type="submit" class="btn btn-success" :disabled="$v.formData.$invalid">{{$t("loginFormSubmit")}}</button>
    </div>

    <loading :loading="loading"></loading>

  </form>
</template>


<script>
import { required, email } from 'vuelidate/lib/validators'
import userService from '@/services/userService'

export default {
  validations: {
    formData: {
      email: {
        required,
        email,
      },
    },
  },
  data() {
    return {
      formData: {
        email: null,
      },
      loading: false,
      errMsg: null,
    }
  },
  methods: {
    submit() {
      if (!this.$v.formData.$invalid) {
        this.loading = true
        userService.requestPassReset(this.formData.email)
          .then(() => {
            this.$emit('success')
          })
          .catch(errRes => {
            this.errMsg = this.$t(errRes.message)
          })
          .finally(() => {
            this.loading = false
          })
      }
    },
  },
}
</script>



