<template>
  <form @submit.prevent="submit" class="register-form">

    <div class="alert alert-danger text-center" v-if="errMsg">
      {{ errMsg }}
    </div>

    <div class="form-group inline-form-group">
      <label class="col-form-label">{{$t("FirstName")}}:</label>
      <div>
        <input type="text"
          class="form-control"
          :class="[{ 'is-invalid': !$v.formData.firstName.required && $v.formData.firstName.$dirty }]"
          @blur="$v.formData.firstName.$touch()"
          v-model="formData.firstName">
        <div class="invalid-feedback">Please, enter your first name.</div>
      </div>
    </div>

    <div class="form-group inline-form-group">
      <label class="col-form-label">{{$t("LastName")}}:</label>
      <div>
        <input type="text"
          class="form-control"
          :class="[{ 'is-invalid': !$v.formData.lastName.required && $v.formData.lastName.$dirty }]"
          @blur="$v.formData.lastName.$touch()"
          v-model="formData.lastName">
        <div class="invalid-feedback">Please, enter your last name.</div>
      </div>
    </div>

    <div class="form-group inline-form-group">
      <label class="col-form-label">{{$t("Phone")}}:</label>
      <div>
        <input type="text"
          class="form-control"
          :class="[{ 'is-invalid': !$v.formData.phone.required && $v.formData.phone.$dirty }]"
          @blur="$v.formData.phone.$touch()"
          v-model="formData.phone">
        <div class="invalid-feedback">Please, enter your phone number.</div>
      </div>
    </div>

    <div class="form-group inline-form-group">
      <label class="col-form-label">{{$t("Email")}}:</label>
      <div>
        <input type="text"
          class="form-control"
          :class="[{ 'is-invalid': (!$v.formData.email.required || !$v.formData.email.email) && $v.formData.email.$dirty }]"
          @blur="$v.formData.email.$touch()"
          v-model="formData.email">
        <div class="invalid-feedback">{{ $v.formData.email.required ? 'Please, enter valid email' : 'Please, enter email' }}</div>
      </div>
    </div>

    <div class="form-group inline-form-group">
      <label class="col-form-label">{{$t("Password")}}:</label>
      <div>
        <input type="password"
          class="form-control"
          :class="[{ 'is-invalid': !$v.formData.password.required && $v.formData.password.$dirty }]"
          @blur="$v.formData.password.$touch()"
          v-model="formData.password">
        <div class="invalid-feedback">Please, enter a password.</div>
      </div>
    </div>

    <div class="text-center py-3">
      <button type="submit" class="btn btn-success" :disabled="$v.formData.$invalid">{{$t("regFormSubmit")}}</button>
    </div>

    <loading :loading="loading"></loading>

  </form>
</template>


<script>
import { required, email } from 'vuelidate/lib/validators'

export default {
  validations: {
    formData: {
      firstName: {
        required,
      },
      lastName: {
        required,
      },
      phone: {
        required,
      },
      password: {
        required,
      },
      email: {
        required,
        email,
      },
    },
  },
  data() {
    return {
      formData: {
        firstName: null,
        lastName: null,
        phone: null,
        password: null,
        email: null,
      },
      loading: false,
      errMsg: null,
    }
  },
  methods: {
    submit() {
      if (!this.$v.formData.$invalid) {
        this.loading = true
        this.$store.dispatch('register', this.formData)
          .then(() => {
            this.$emit('success')
          })
          .catch(errRes => {
            this.errMsg = this.$t(errRes.error)
          })
          .finally(() => {
            this.loading = false
          })
      }
    },
  },
}
</script>



