<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-5 mx-auto pt-5">

        <div class="card" v-if="!isActionSuccessful">
          <h2 class="card-header text-center">{{$t("resetPageTitle")}}</h2>
          <div class="card-body">
            <reset-pass-form @success="isActionSuccessful = true" :token="token"></reset-pass-form>
          </div>
          <div class="card-footer text-center">
            <router-link to="/login">{{$t("Login")}}</router-link>&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;<router-link to="/forgot">Forgot password?</router-link>
          </div>
        </div><!-- .card -->

        <div v-if="isActionSuccessful" class="text-center">
          <p>{{$t("Password reset was successfull")}}.</p>
          <b-button size="lg" variant="success" to="/login">{{$t("Login")}}</b-button>
          <!-- ?? <router-link to="/login">{{$t("Login")}}</router-link>&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;<router-link to="/register">{{$t("Register")}}</router-link> -->
        </div>

      </div>
    </div>
  </div>
</template>


<script>
import ResetPassForm from '@/components/forms-auth/ResetPassForm'

export default {
  components: {
    ResetPassForm,
  },
  props: ['token'],
  data() {
    return {
      isActionSuccessful: false,
    }
  },
}
</script>



