<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-5 mx-auto pt-5">

        <div class="card">
          <h2 class="card-header text-center">{{$t("loginPageTitle")}}</h2>
          <div class="card-body">
            <login-form @success="onLogin"></login-form>
          </div>
          <div class="card-footer text-center">
            <router-link to="/register">{{$t("Register")}}</router-link>&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;<router-link to="/forgot">Forgot password?</router-link>
          </div>
        </div><!-- .card -->

      </div>
    </div>
  </div>
</template>


<script>
import LoginForm from '@/components/forms-auth/LoginForm'

export default {
  components: {
    LoginForm,
  },
  methods: {
    onLogin() {
      this.$router.push('/')
    },
  },
}
</script>



