import axios from 'axios'
import {apiToCompany, apiToCompanyShort, companyToApi} from './companyConverters'

const COMPANIES_API = '/Companies/'


export default {
  // LIST OF MY COMPANIES
  getMyCompanies() {
    return axios.get(COMPANIES_API)
      .then(data => data.map(apiToCompanyShort))
  },
  // CRUD
  create(companyData) {
    const postData = {
      name: companyData.name,
      countryId: companyData.country.id,
      currencyId: companyData.currency.id,
    }

    if (companyData.regNum) {
      postData.registrationNumber = companyData.regNum
    }

    if (companyData.parent) {
      postData.parentId = companyData.parent.id
    }

    return axios.post(COMPANIES_API, postData)
      .then(apiToCompany)
  },
  getCompany(company) {
    return axios.get(COMPANIES_API + company.id)
      .then(apiToCompany)
  },
  update(company, updatedData) {
    const postData = companyToApi(updatedData)
    return axios.patch(COMPANIES_API + company.id, postData)
      .then(apiToCompany)
  },
  delete(company) {
    return axios.delete(COMPANIES_API + company.id)
  },
  // Misc
  uploadLogo(company, imgFile) {
    let fd = new FormData()
    fd.append('file', imgFile)
    return axios.post(COMPANIES_API + company.id + '/Logo', fd)
  },
}



