<template>
  <div class="table-responsive collaborators-table">
    <table class="table table-striped">
      <thead>
        <tr>
          <th v-sort="{sortKey: 'name', compare: 'strings'}">Name</th>
          <th v-sort="{sortKey: 'email', compare: 'strings'}">Email</th>
          <th v-sort="{sortKey: 'role.id', compare: 'numbers', initOrder: 'ASC'}">Role</th>
          <th v-sort="{sortKey: 'isPending', compare: 'bool'}">Status</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <row v-for="collaborator in pageItems" :key="collaborator.email" :collaborator="collaborator"></row>
      </tbody>
    </table>

    <loading :loading="loading"></loading>

  </div>
</template>


<script>
import tableTools from '@/table-tools'
import Row from './CollaboratorsTableRow'

export default {
  name: 'CollaboratorsTable',
  components: {
    Row,
  },
  directives: {
    Sort: tableTools.SortDirective(tableTools.tableStore),
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    pageItems() {
      tableTools.tableStore.commit('setItems', this.$store.getters.collaborators)
      return tableTools.tableStore.getters.pageItems()
    },
  },
}
</script>



