<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!cProp.num"
           @edit="onEdit">

    <div class="cp-row">
      <label>Country:</label>
      <c-form-select class="form-control"
                    name="country"
                    v-validate="'required'"
                    :class="[{ 'is-invalid': errors.has('country')}]"
                    v-model="cpModel.country"
                    :plain="true"
                    :disabled="!editMode"
                    :options="countries"
                    text-field="name">
      </c-form-select>
    </div>


    <div class="cp-row">
      <label>{{ (cpModel.countryId === 1) ? 'VAT ID' : 'Sales Tax ID' }}:</label>
      <input
             type="text"
             class="form-control"
             v-model="cpModel.num"
             name="num"
             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('num')}]"
             :disabled="!editMode" />
    </div>


    <div class="cp-row">
      <label>Reporting Regularity:</label>
      <input
             type="text"
             class="form-control"
             v-model="cpModel.reportingRegularity"
             name="repReg"
             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('repReg')}]"
             :disabled="!editMode" />
    </div>


  </cp-form>
</template>


<script>
import merge from 'lodash.merge'
import basePropMixin from './basePropMixin'

const vatVueConfig = {
  created() {
    // TODO: ?? does it work, shoud use $ref na CpForm??
    this.loading = true
    this.$store.dispatch('loadCountries')
      .finally(() => {
        this.loading = false
      })
  },
  props: {
    cProp: {
      required: false,
      default() {
        return {}
      },
    },
  },
  data() {
    return {
      cpModel: Object.assign({}, this.cProp),
      editMode: !this.cProp.num,
    }
  },
  computed: {
    countries() {
      return this.$store.getters.countries
    },
  },
  methods: {
    save() {
      const action = this.cProp.num ? 'updateSCVat' : 'createSCVat'
      return this.$store.dispatch(action, this.cpModel)
        .then(() => {
          this.$emit('success')
        })
    },
    remove() {
      return this.$store.dispatch('deleteSCVat', this.cProp)
        .then(() => {
          this.$emit('remove')
        })
    },
  },
}

export default merge({}, basePropMixin, vatVueConfig)
</script>



