<template>
  <div class="table-responsive collaborators-table">
    <table class="table table-striped">
      <thead>
        <tr>
          <th v-sort="{sortKey: 'name', compare: 'strings', initOrder: 'ASC'}">Name</th>
          <th>Aliases</th>
          <th v-sort="{sortKey: 'regNum', compare: 'strings'}">Reg Number</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="company in pageItems" :key="company.id">
          <th>
            <company-card :company="company" size="m"></company-card>
          </th>
          <td>{{ company.aliases.map(i => i.aname).join(', ') }}</td>
          <td>{{ company.regNum }}</td>
          <td>
            <div class="dropdown">
              <button class="btn btn-secondary btn-sm" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">...</button>
              <div class="dropdown-menu dropdown-menu-right">
                <a class="dropdown-item" href="#">Upload</a>
                <a class="dropdown-item" href="#">Start Reconciliation</a>
              </div>
            </div>
          </td>
        </tr>
      </tbody>
    </table>

    <loading :loading="loading"></loading>

  </div>
</template>


<script>
import tableTools from '@/table-tools'


export default {
  name: 'ContactsTable',
  directives: {
    Sort: tableTools.SortDirective(tableTools.tableStore),
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    pageItems() {
      tableTools.tableStore.commit('setItems', this.$store.getters.contacts)
      return tableTools.tableStore.getters.pageItems()
    },
  },
}
</script>



