<template>
  <form @submit.prevent="submit" class="company-property-row">

    <div class="cp-col">

      <div class="alert alert-danger" v-if="errMsg">
        {{ errMsg }}
      </div>

      <slot></slot>

    </div>

    <div class="cp-actions-col" v-if="!isEditMode">
      <button type="button" class="btn btn-info btn-sm" @click="edit">Edit</button>
      <button type="button" class="btn btn-danger btn-sm" @click="del" v-if="remove">Delete</button>
    </div>

    <div class="cp-actions-col" v-if="isEditMode">
      <button type="submit" class="btn btn-success btn-sm">Save</button>
      <button type="button" class="btn btn-light btn-sm" @click="cancelEdit">Cancel</button>
    </div>

    <loading :loading="loading"></loading>

  </form>
</template>


<script>
export default {
  props: {
    save: {
      required: true,
      type: Function,
    },
    remove: {
      type: Function,
    },
    cancel: {
      required: true,
      type: Function,
    },
    validate: {
      required: true,
      type: Function,
    },
    editMode: {
      default: false,
    },
  },
  data() {
    return {
      isEditMode: this.editMode,
      errMsg: null,
      loading: false,
    }
  },
  methods: {
    submit() {
      this.loading = true
      this.errMsg = null
      this.validate()
        .then(result => {
          // result comes from vee-validate
          if (result) {
            return this.save()
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .then(() => {
          this.cancelEdit()
        })
        .catch(errRes => {
          this.errMsg = this.$t(errRes.message)
        })
        .finally(() => {
          this.loading = false
        })
    },
    cancelEdit() {
      this.cancel()
      this.isEditMode = false
      this.errMsg = null
    },
    edit() {
      this.isEditMode = true
    },
    del() {
      this.loading = true
      this.remove()
        .catch(errRes => {
          this.errMsg = this.$t(errRes.message)
        })
        .finally(() => {
          this.loading = false
        })
    },
  },
  watch: {
    isEditMode(toggle) {
      this.$emit('edit', toggle)
    },
  },
}
</script>



