<template>
  <form @submit.prevent="submit" class="my-company-form">

    <div class="alert alert-danger text-center" v-if="errMsg">
      {{ errMsg }}
    </div>

    <!-- <h1>TODO: Logo</h1> -->

    <div class="form-group">
      <label for="name">Company Name:</label>
      <input type="text"
             class="form-control"
             id="name"
             name="name"
             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('name')}]"
             v-model="formData.name"
             :disabled="!editMode" />
      <small class="form-text text-muted">The complete registered name.</small>
    </div>

    <div class="form-group">
      <label for="regId">Registration ID:</label>
      <input type="text"
             class="form-control"
             id="regId"
             v-model="formData.regNum"
             :disabled="!editMode" />
    </div>

    <div class="form-row">
      <div class="form-group col">
        <label for="country">Country:</label>
        <c-form-select id="country"
                      name="country"
                      v-validate="'required'"
                      :class="[{ 'is-invalid': errors.has('country')}]"
                      v-model="formData.country"
                      :plain="true"
                      :disabled="!editMode"
                      :options="countries"
                      text-field="name">
        </c-form-select>
      </div>

      <div class="form-group col">
        <label for="currency">Reporting Currency:</label>
        <c-form-select id="currency"
                      name="currency"
                      v-validate="'required'"
                      :class="[{ 'is-invalid': errors.has('currency')}]"
                      v-model="formData.currency"
                      :plain="true"
                      :disabled="!editMode"
                      :slot-options="currencies">
          <option v-for="currency in currencies" :value="currency.id">{{ currency.code }} - {{ currency.name }}</option>
        </c-form-select>
      </div>
    </div>

    <div class="form-group">
      <label for="address">Address:</label>
      <input type="text"
             class="form-control"
             id="address"
             v-model="formData.address"
             :disabled="!editMode" />
    </div>

    <div class="form-group" v-if="myCompanies.length">
      <label for="parent">Parent company:</label>
      <c-form-select id="parent"
                    v-model="formData.parent"
                    :plain="true"
                    :disabled="!editMode"
                    :options="myCompanies"
                    text-field="name">
      </c-form-select>
    </div>

    <div class="text-center py-3" v-if="editMode">
      <button type="submit" class="btn btn-primary">Save</button>
      <button type="button" class="btn btn-light" @click="cancelEdit">Cancel</button>
    </div>

    <div class="text-center py-3" v-if="!editMode">
      <button type="button" class="btn btn-info" @click="edit">Edit</button>
    </div>

    <loading :loading="loading"></loading>

  </form>
</template>


<script>
const INITIAL_COMPANY_DATA = {
  name: null,
  regNum: null,
  country: null,
  currency: null,
  address: null,
  parent: null,
  logo: null,
}

// TODO: check user's role
// if he is not allowed to change parent

export default {
  created() {
    this.loading = true

    var countriesPromise = this.$store.dispatch('loadCountries')
    var currenciesPromise = this.$store.dispatch('loadCurrencies')
    var myCompaniesPromise = this.$store.dispatch('loadMyCompanies')

    Promise.all([countriesPromise, currenciesPromise, myCompaniesPromise])
      .finally(() => {
        this.loading = false
      })
  },
  props: {
    company: {
      default() {
        return INITIAL_COMPANY_DATA
      },
    },
  },
  data() {
    return {
      formData: Object.assign({}, this.company),
      editMode: !this.company.name,
      errMsg: null,
      loading: false,
    }
  },
  computed: {
    countries() {
      return this.$store.getters.countries
    },
    currencies() {
      return this.$store.getters.currencies
    },
    myCompanies() {
      return this.$store.getters.myCompanies
    },
  },
  methods: {
    validate() {
      return this.$validator.validateAll()
    },
    submit() {
      this.loading = true
      this.validate()
        .then(result => {
          if (result) {
            const action = this.company.name ? 'updateSelCompany' : 'createCompany'
            return this.$store.dispatch(action, this.formData)
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .then(company => {
          this.$emit('success', company)
        })
        .catch(errRes => {
          this.errMsg = this.$t(errRes.message)
        })
        .finally(() => {
          this.loading = false
        })
    },
    cancelEdit() {
      this.$emit('cancel')
      this.editMode = false
      this.errMsg = null
    },
    edit() {
      this.editMode = true
    },
  },
  watch: {
    company(newCompany) {
      this.editMode = !newCompany.name
      this.formData = Object.assign({}, newCompany)
      this.errMsg = null
    },
  },
}
</script>



